@keyframes slide {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -120px 60px;
  }
}

.InfoBanner {
  position: absolute;
  left: 0;
  bottom: 10px;
  width: 100%;
}
.fadeIn {
  animation: fadeIn 0.6s linear;
  animation-fill-mode: forwards;
}
.fadeOut {
  animation: fadeOut 0.6s linear;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.reversed {
  display: inline-block;
  padding: 0.3em;
  padding-left: 0.3em;
  margin-left: 0.8em;
  position: relative;
  text-align: center;
  vertical-align: middle;
  line-height: 1;
  color: #fff;
  font-size: 15px;
  background-color: #f1453d;
}
.reversed:before,
.reversed:after {
  content: '';
  width: 0;
  height: 0;
  right: -0.8em;
  position: absolute;
  top: 0;
  border-top: 0.8em solid #f1453d;
}
.reversed:after {
  top: auto;
  bottom: 0;
  border-top: none;
  border-bottom: 0.8em solid #f1453d;
}
.reversedRight:before,
.reversedRight:after {
  border-right: 0.8em solid transparent;
  right: -0.8em;
}
.reversedRight {
  width: 17px;
  border-radius: 5px 0px 0px 5px;
  animation: tilt 2s infinite;
}
@keyframes tilt {
  0% {
    left: 0%;
  }
  50% {
    left: 4px;
  }
  100% {
    left: 0px;
  }
}
.reversedLeft {
  margin-left: 15px;
  border-radius: 0px 5px 5px 0px;
}
.reversedLeft:before,
.reversedLeft:after {
  content: '';
  width: 0;
  height: 0;
  border-left: 0.8em solid transparent;
  left: -0.8em;
  position: absolute;
}
.reversedLeft:after {
  top: auto;
  bottom: 0;
  border-top: 0.8em solid #f1453d;
  border-bottom: none;
}
.reversedLeft:before {
  bottom: auto;
  top: 0;
  border-bottom: 0.8em solid #f1453d;
  border-top: none;
}

.bgAnimation {
  background-color: #f1453d;
  background-image: linear-gradient(
      45deg,
      #e57373 25%,
      transparent 25%,
      transparent 75%,
      #e57373 75%,
      #e57373
    ),
    linear-gradient(
      -45deg,
      #e57373 25%,
      transparent 25%,
      transparent 75%,
      #e57373 75%,
      #e57373
    );
  background-size: 60px 60px;
  animation: slide 4s infinite linear;
}
